import React, { useState } from 'react';
import { BottomNavigation, BottomNavigationAction, makeStyles } from '@material-ui/core';
import PersonIcon from '@material-ui/icons/Person';
import TerrainIcon from '@material-ui/icons/Terrain';

interface NavBarProps {
  defaultTab: NavItems;
}

export enum NavItems {
  buckets,
  profile,
}

const useStyles = makeStyles(theme => ({
  offset: theme.mixins.toolbar,
  navBar: {
    position: 'fixed',
    bottom: 0,
    top: 'auto',
    left: 0,
    right: 0,
  },
}));

export const NavBar: React.FC<NavBarProps> = ({ defaultTab }) => {
  const [value, setValue] = useState(defaultTab);
  const classes = useStyles();

  return (
    <>
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        showLabels
        className={classes.navBar}
      >
        <BottomNavigationAction label="Kupki" value={NavItems.buckets} icon={<TerrainIcon />} />
        <BottomNavigationAction label="Profil" value={NavItems.profile} icon={<PersonIcon />} disabled={true} />
      </BottomNavigation>
      <div className={classes.offset} />
    </>
  );
};
